body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
  background-image: url(/gotiscoming/static/media/night.a719f64d.jpg);
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer {
  position: fixed;
  left: 10px;
  bottom: 8px;
  opacity: 0.4;
  text-align: center;
}

footer span {
  color: white;
}

footer a {
  color: white;
}

.clock {
    height: 8rem;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    font-size: 110px;
    font-family: sans-serif;
    letter-spacing: 5px;
    text-shadow: 0 0 10px #fff;
    text-transform: uppercase;
}

.clock-header {
    margin: 13px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.clock-sub-header {
    margin-bottom: 10px;
    font-size: 13px;
    letter-spacing: normal;
    letter-spacing: initial;
}

.wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

